.footer-main {
  height: 15rem;
  margin-top: 3rem;
  display: flex;
  background-color: #000;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
}

.footer-divs {
  background-color: #000;
  width: 25rem;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  color: #aaa;
  text-align: left;
  margin-left: 5rem;
}

.footer-text-main {
  margin-top: 1rem;
  margin-bottom: 0rem;
  color: #eee;
  margin-right: 1rem;
  font-weight: 600;
}

.footer-divs p:hover {
  color: #fff;
  cursor: pointer;
}